import { Box, Typography } from "@mui/material";
import { mainTheme } from "../../../App/Theme/mainTheme";

export const FooterComponent = () => {
  let currentYear = new Date().getFullYear();

  return (
    <footer
      style={{
        backgroundColor: mainTheme.palette.primary.main,
        minHeight: "50px",
        padding: "12px",
      }}
    >
      <Box sx={{ padding: "5px" }}>
        <Typography color="default.main">@{currentYear} ООО "Эркер"</Typography>
      </Box>
      <Box sx={{ padding: "5px" }}>
        <Typography color="default.main">
          Thanks to{" "}
          <a target="_blank" href="https://icons8.com" rel="noreferrer">
            Icons8
          </a>
          ,{" "}
          <a
            target="_blank"
            href="https://www.pexels.com/ru-ru/"
            rel="noreferrer"
          >
            Pexels
          </a>
          ,<a href="https://www.vecteezy.com/free-vector/modern"> Vecteezy</a>
        </Typography>
      </Box>
      <a
        href="https://metrika.yandex.ru/stat/?id=87434621&amp;from=informer"
        target="_blank"
        rel="nofollow noreferrer"
      >
        <img
          src="https://informer.yandex.ru/informer/87434621/3_0_ECECECFF_CCCCCCFF_0_pageviews"
          style={{ width: "80px", height: "31px", border: "0" }}
          alt="Яндекс.Метрика"
          title="Яндекс.Метрика: данные за сегодня (просмотры, визиты и уникальные посетители)"
          className="ym-advanced-informer"
          data-cid="87434621"
          data-lang="ru"
        />
      </a>
    </footer>
  );
};
